* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
}

html {
  color: #ff0;
  background: url("background.60c60f7e.jpg") 0 0 / cover fixed;
}

.loader {
  width: 48px;
  height: 48px;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader:after, .loader:before {
  content: "";
  box-sizing: border-box;
  width: 48px;
  height: 48px;
  border: 2px solid #f8ec02;
  border-radius: 50%;
  animation: animloader 1s linear infinite;
  position: absolute;
  top: 0;
  left: 0;
}

.loader:after {
  animation-delay: .5s;
}

@keyframes animloader {
  0% {
    opacity: 1;
    transform: scale(0);
  }

  100% {
    opacity: 0;
    transform: scale(1);
  }
}

.cat-img {
  border-radius: 10px;
  box-shadow: 0 0 0 10px rgba(255, 225, 53, .84);
}

.cat-wrapper {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.cat-title {
  padding: 30px 0;
}

.cat-description {
  padding: 70px 320px 20px;
}

.cat-temperament {
  padding: 0 320px 70px;
}

.description-wrapper {
  background-color: rgba(127, 255, 251, .6);
  margin-top: 20px;
}

